@keyframes move {
     from {
         right: -300px;
     }
     to {
         right: 0px;
     }
 }

@keyframes moveBack {
    from {
        right: 0px;
    }
    to {
        right: -300px;
    }
}


@keyframes rotate {
  from {
    transform: rotate(0);
  }

  to {
    transform: rotate(360deg);
  }
}

@keyframes fadeIn {
  from {
    opacity: 0;
  }

  to {
    opacity: 1;
  }
}

@keyframes fadeOut {
  from {
    opacity: 1;
  }

  to {
    opacity: 0;
  }
}

@keyframes animationShow {
  from {
    height: 0;
  }

  to {
    height: 300px;
  }
}

@keyframes animationUnshow {
  from {
    height: 300px;
  }

  to {
    height: 0px;
  }
}
