.formFreeTrial {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  margin: 0 15px;
  padding-top: 50px;
  overflow: hidden;

  .formFreeTrial__left-side {

    padding-bottom: 40px;
     img {
       width: 200px;
       height: 220px;
     }
  }

  .formFreeTrial__right-side {
    .formFreeTrial__title {
      font-style: normal;
      font-weight: bold;
      font-size: 40px;
      line-height: 40px;
      letter-spacing: -0.357143px;
      color: $defaultText;
      padding-bottom: 18px;
    }

    .formFreeTrial__subtitle {
      font-style: normal;
      font-weight: normal;
      font-size: 24px;
      line-height: 34px;
      color: $defaultText;
      padding-bottom: 40px;
    }

    .formFreeTrial__email {
      background: $white;
      border: 1px solid rgba(43, 41, 45, 0.2);
      box-sizing: border-box;
      border-radius: 2px;
      width: 100%;
      height: 48px;
      margin-bottom: 12px;
    }

    .formFreeTrial__email:focus {
      outline: 1px solid $blue;
    }

    .formFreeTrial__email[placeholder] {
      font-style: normal;
      font-weight: normal;
      font-size: 18px;
      line-height: 13px;
      text-align: center;
      color: $defaultText;
    }

    .formFreeTrial__text {
      margin-top: 10px;
      margin-bottom: 50px;
      font-style: normal;
      font-weight: normal;
      font-size: 14px;
      line-height: 22px;
      color: rgba(43, 41, 45, 0.6);
    }

    .formFreeTrial__link {
      text-decoration: none;
      color: $blueText;
      transition: .2s ease;
    }
    .formFreeTrial__link:hover {
      color: $defaultText;
      transition: .2s ease;
    }
  }
}

@include media($_768) {
  .formFreeTrial {
    flex-direction: row;
    justify-content: space-evenly;
    padding-bottom: 72px;

    .formFreeTrial__left-side {
      padding: 0;
      img {
        width: 270px;
        height: 296px;
      }
    }

    .formFreeTrial__right-side {
      max-width: 560px;

      .formFreeTrial__subtitle {
        width: 560px;
      }
      .formFreeTrial__form {
        form{
          display: flex;

          .formFreeTrial__email {
            width: 300px;
            height: 48px;
          }

          .formFreeTrial__btn {
            margin-left: 10px;
          }

          .formFreeTrial__email[placeholder] {
            font-style: normal;
            font-weight: normal;
            text-align: left;
            font-size: 18px;
            line-height: 13px;
            color: $defaultText;
            padding-left: 10px;
          }
        }
      }
    }
  }
}

@include media($_992) {
  .formFreeTrial {
    justify-content: center;
    .formFreeTrial__right-side .formFreeTrial__form form .formFreeTrial__email{
      width: 385px;
    }
    .formFreeTrial__left-side {
      margin-right: 120px;
    }
  }
}
