.btn {
    width: 100%;
    padding: 17px 0;
    font-weight: bold;
    line-height: 13px;
    font-size: 18px;
    border-radius: 2px;
    border: none;
    cursor: pointer;
    transition: ease .2s;
}

.btn:hover {
    /*box-shadow: 3px 5px 6px rgba(0,0,0,0.4);*/
    box-shadow: 0px 11px 55px rgba(0, 0, 0, 0.07), 0px 13px 18px rgba(0, 0, 0, 0.15);
}

.btn__blue {
    color: #fff;
    background: linear-gradient(180deg, rgba(255, 255, 255, 0.05) 0%, rgba(255, 255, 255, 1e-05) 100%), #3040C4;
    background-blend-mode: overlay, normal;
    box-shadow: 0px 4px 6px rgba(0, 0, 0, 0.05), 0px 1px 3px rgba(0, 0, 0, 0.1), inset 0px 1px 0px rgba(255, 255, 255, 0.1);
}

.btn__white {
    color: #3040C4;
    background: linear-gradient(180deg, rgba(255, 255, 255, 0.05) 0%, rgba(255, 255, 255, 1e-05) 100%), #FFFFFF;
    background-blend-mode: overlay, normal;
    box-shadow: 0px 4px 6px rgba(0, 0, 0, 0.05), 0px 1px 3px rgba(0, 0, 0, 0.1), inset 0px 1px 0px rgba(255, 255, 255, 0.1);
}

@media (min-width: 768px) {
    .btn {
        width: auto;
        height: 48px;
        padding: 12px 28px 15px 28px;
        font-weight: bold;
        line-height: 13px;
        font-size: 15px;
        border-radius: 2px;
        border: none;
        cursor: pointer;
    }
}

@media (min-width: 992px) {
    .btn {
        font-size: 18px;
    }
}

