.container-banner {
    background-color: $bgcBanner;
    position: relative;
    padding-bottom: 36px;
}
.banner-info__title {
    font-family: Work Sans;
    font-style: normal;
    font-weight: bold;
    font-size: 50px;
    line-height: 50px;
    color: $defaultText;
    padding-top: 35px;
    padding-bottom: 24px;
}
.banner-info {
    margin: 0 15px;
}
.banner-info__text {
    font-size: 24px;
    line-height: 34px;
    color: $defaultText;
    padding-bottom: 30px;
}
.banner-img img{
    width: inherit;
    height: inherit;
}
.banner-info__buttons button:first-child {
    margin-bottom: 15px;
}
.banner-info__buttons {
    margin-bottom: 33px;
}
.banner-img {
    width: 100%;
}

@include media($_768) {
    .banner {
        background-color: $bgcBanner;
        margin: 0 auto;
        background-repeat: no-repeat;
        background-position: 67% 73%;
    }
    .banner-info__title {
        font-weight: bold;
        line-height: 60px;
        font-size: 60px;
        z-index: 2;
        width: 520px;
        position: relative;
        margin-bottom: 22px;
        padding: 0;
    }
    .banner-info__text {
        font-weight: normal;
        font-size: 23px;
        line-height: 34px;
        color: $defaultText;
        width: 456px;
        height: 102px;
        margin-bottom: 18px;
        padding: 0;
    }
    .container-banner {
        margin: 0 auto;
        max-width: 1140px;
        padding: 25px 0;
    }
    .banner-info__buttons .btn:last-child {
        margin-left: 20px;
    }
    .banner-info__buttons {
        display: flex;
        justify-content: center;
    }
    .burger-menu {
        display: none;
    }
    .banner-img {
        position: absolute;
        top: 46px;
        left: 428px;
        width: 331px;
    }
    .banner-info__buttons button:first-child {
        margin-bottom: 0;
    }
}

@include media(852px) {
    .banner-img {
        top: 33px;
        left: 460px;
        width: 387px;
    }
}

@include media(908px) {
    .banner-img {
        top: 33px;
        left: 500px;
        width: 400px;
    }
}

@include media($_992) {
    .banner-info__buttons {
        display: block;
    }
    .banner-info__buttons .btn:last-child {
        margin-left: 8px;
    }
    .banner-img {
        position: absolute;
        top: 31px;
        left: 450px;
        width: 530px;
        height: 316px;
    }
}

@include media($_1200) {
    .banner {
        background-color: $bgcBanner;
        margin: 0 auto;
        background-repeat: no-repeat;
        background-position: 67% 73%;
    }
    .banner-info__title {
        font-weight: bold;
        line-height: 78px;
        font-size: 80px;
        z-index: 2;
        width: 754px;
        height: 165px;
        position: relative;
        margin-bottom: 34px;
        padding: 0;
    }
    .banner-info__text {
        font-weight: normal;
        font-size: 24px;
        line-height: 34px;
        color: $defaultText;
        width: 456px;
        height: 102px;
        margin-bottom: 58px;
        padding: 0;
    }
    .container-banner {
        padding: 45px 0;
    }
    .banner-img {
        position: absolute;
        top: 54px;
        left: 454px;
        width: 694px;
        height: 412px;
    }
}

@include media($_1400) {
    .container-banner {
        margin: 0 auto;
        max-width: 1140px;
        padding: 75px 0;
    }
    .banner-img {
        position: absolute;
        top: 84px;
        left: 470px;
        width: 796px;
        height: 445px;
    }
}
