.footer {
  ul {
    list-style: none;
  }
  .footer-icons {
    padding-bottom: 40px;

    .footer-icons__png img {
      margin-right: 10px;
      transition: ease .3s;
    }

    .footer-icons__png img:hover{
      transition: ease .3s;
      box-shadow: 0px 11px 55px rgba(0, 0, 0, 0.07), 0px 13px 18px rgba(0, 0, 0, 0.15);
      border-radius: 7px;
    }
  }
  .footer__main {
    display: flex;
    flex-direction: column;
    align-items: center;

    .footer__main-company {
      align-self: flex-start;
      padding-left: 15px;
      padding-bottom: 30px;

      .footer__company-title {
        font-style: normal;
        font-weight: bold;
        font-size: 22px;
        line-height: 28px;
        color: $defaultText;
        margin-bottom: 16px;
      }

      .footer__links ul a{
        text-decoration: none;
        font-style: normal;
        font-weight: normal;
        font-size: 18px;
        line-height: 30px;
        color: $gray;
        transition: ease .2s;
      }
      .footer__links ul a:hover {
        transition: ease .2s;
        color: $defaultText;
      }
    }

    .footer__main-product {
      align-self: flex-start;
      padding-left: 15px;
      padding-bottom: 30px;

    .footer__product-title {
      font-style: normal;
      font-weight: bold;
      font-size: 22px;
      line-height: 28px;
      color: $defaultText;
      margin-bottom: 16px;
    }
      .footer__links ul a{
        text-decoration: none;
        font-style: normal;
        font-weight: normal;
        font-size: 18px;
        line-height: 30px;
        color: $gray;
        transition: ease .2s;
      }
      .footer__links ul a:hover {
        transition: ease .2s;
        color: $defaultText;
      }
    }

    .footer__main-foundation {
      .footer-text {
         display: none;
       }
      display: flex;
      flex-direction: column;
      align-items: center;
      .footer-foundation {
        display: flex;
        flex-direction: column;
        align-items: center;

        .logo__title {
          text-align: left;
          font-style: normal;
          font-weight: bold;
          font-size: 20px;
          line-height: 25px;
          color: $defaultText;
          text-align: center;
        }
        .logo__subtitle {
          font-style: normal;
          font-weight: normal;
          font-size: 18px;
          line-height: 25px;
          color: $gray;
          padding-bottom: 31px;
        }

        .logo__img {
          width: 60px;
          height: 60px;
        }
        .logo__text {
          text-align: center;
        }
      }
    }
  }

  /*Нижняя часть футера*/

  .footer__ext {
    margin: 15px auto;
    max-width: 80%;
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;

    .footer__ext-left {
      display: flex;
      justify-content: space-between;
      width: 182px;
      padding-bottom: 30px;
      padding-right: 104px;
      a {
        text-decoration: none;
        font-style: normal;
        font-weight: normal;
        font-size: 14px;
        line-height: 22px;
        color: $gray;
        transition: ease .2s;
      }
      a:hover {
        color: $defaultText;
        transition: ease .2s;
      }

    }
    .footer__ext-right {
      width: 98px;
      height: 18px;
      display: flex;
      justify-content: space-between;
      padding-bottom: 15px;

      .footer-icon {
        transition: ease .2s;
      }
      
      a:hover {
        .footer-icon {
          fill: blue;
          transition: ease .2s;
        }
      }
    }
  }
}

@include media($_480) {
  .footer {
    .footer__main {
      flex-direction: row;
      flex-wrap: wrap;
      justify-content: space-evenly;

      .footer__main-foundation{
        width: 100%;
      }
    }
  }
}

@include media($_768) {
  .footer {
    padding-top: 44px;

    .footer-icons {
      padding-bottom: 0;
    }

    .footer__links a{
      font-style: normal;
      font-weight: normal;
      font-size: 18px;
      line-height: 30px;
      color: $gray;
      text-decoration: none;
    }
    .footer__main-company {
      padding-top: 16px;
      align-self: auto;
      padding-left: 0;
      padding-bottom: 0;

      .footer__company-title {
        font-style: normal;
        font-weight: bold;
        font-size: 22px;
        line-height: 28px;
        color: $defaultText;
        margin-bottom: 18px;
      }
    }
    .footer__main-product {
      padding-top: 16px;
      align-self: auto;
      padding-left: 0;
      padding-bottom: 0;

      .footer__product-title {
        font-style: normal;
        font-weight: bold;
        font-size: 22px;
        line-height: 28px;
        color: $defaultText;
        margin-bottom: 18px;
      }
    }
    .footer__main {
      flex-direction: row;
      align-items: flex-start;
      flex-wrap: nowrap;
      padding-bottom: 59px;
      border-bottom: 1px solid rgba(43,41,45,0.1);
      .footer__company-links {
        max-width: 265px;
      }
      .footer__product-links {
        max-width: 265px;
        margin-right: 28px;
      }

      .footer__main-foundation {
        width: 360px;
        height: 235px;
        margin-left: 50px;
        align-items: flex-start;

        .footer-text {
          display: block;
        }
        .footer-foundation{
          display: flex;
          flex-direction: row;
          align-items: end;
          margin-bottom: 30px;
          .logo__img {
            width: 60px;
            height: 60px;
            margin-left: 0;
          }
          .logo__title {
            text-align: left;
          }
          .logo__subtitle {
            padding-bottom: 0;
          }
        }
        .footer-text {
          width: 360px;
          height: 85px;
          font-style: normal;
          font-weight: normal;
          font-size: 18px;
          line-height: 26px;
          color: $gray;
          margin-bottom: 25px;
        }
      }
    }
  }
}

@include media($_992) {
  .footer {
    .footer__main {
      justify-content: space-evenly;

      .footer__main-foundation {
        margin-left: 130px;
      }
    }
  }
}
