.visitors {
  background: rgba(0, 161, 198, .13);

  .visitors__subtitle {
    padding-top: 50px;
    padding-bottom: 17px;
    font-style: normal;
    font-weight: bold;
    font-size: 22px;
    line-height: 34px;
    text-align: center;
    color: $defaultText;
  }

  .visitors__title {
    font-style: normal;
    font-weight: bold;
    font-size: 40px;
    line-height: 40px;
    text-align: center;
    letter-spacing: -0.357143px;
    color: $defaultText;
    padding-bottom: 18px;
  }

  .visitors__paragraph {
    font-family: Work Sans;
    font-style: normal;
    font-weight: normal;
    font-size: 24px;
    line-height: 34px;
    text-align: center;
    color: $defaultText;
    padding-bottom: 30px;
  }

  .visitors__button-container {
    max-width: 290px;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    margin: 0 auto;
    padding-bottom: 40px;

      .btn {
        width: 100%;
        height: 48px;
        margin-bottom: 12px;
      }
  }

  .visitors__content-img {
    margin: 0 auto;
    display: flex;
    max-width: 290px;
    padding-bottom: 50px;
    height: max-content;
    flex-direction: column;
    align-items: center;
  }

  .visitors__img-item {
    width: 290px;
    height: 300px;
    display: flex;
    flex-direction: column;
    justify-content: space-evenly;
    align-items: center;
    background: linear-gradient(180deg, rgba(255, 255, 255, 0.05) 0%, rgba(255, 255, 255, 1e-05) 100%), $white;
    background-blend-mode: overlay, normal;
    box-shadow: 0px 4px 6px rgba(0, 0, 0, 0.05), 0px 1px 3px rgba(0, 0, 0, 0.1), inset 0px 1px 0px rgba(255, 255, 255, 0.1);
    border-radius: 8px;
    cursor: pointer;
    margin-bottom: 15px;
  }

  .visitors__img-item:hover  {
    background-color: $blue;

    .visitors__link {
      color: $white;
    }
  }

  .visitors__link {
    font-style: normal;
    font-weight: bold;
    font-size: 22px;
    line-height: 28px;
    color: $defaultText;
    text-decoration: none;
  }
}

@include media($_768) {
  .visitors {
    .visitors__content-img {
      flex-direction: row;
      width: 100%;
      max-width: none;
    }

    .visitors__img-item {
      margin: 0 1vw;
    }

    .visitors__button-container {
      max-width: 330px;
      flex-direction: row;
      padding-bottom: 110px;

      .btn {
        width: auto;
      }
    }
  }
}

@include media($_1200) {
  .visitors {

    .visitors__subtitle {
      padding-top: 108px;
      padding-bottom: 24px;
    }

    .visitors__title {
      font-family: Work Sans;
      font-size: 56px;
      line-height: 56px;
      letter-spacing: -0.5px;
      padding-bottom: 30px;
    }

    .visitors__paragraph {
      padding-bottom: 34px;
    }

    .visitors__button-container {
      width: 328px;
      height: 48px;
      padding-bottom: 111px;
    }

    .btn {
      width: 160px;
      height: 48px;
    }

    .visitors__content-img {
      width: 1139px;
      height: 300px;
      padding-bottom: 110px;
      justify-content: space-between;
    }

    .visitors__img-item {
      width: 262px;
      height: 300px;
      margin: 0;
    }
  }
}
