.container-header {
    max-width: 1140px;
    margin: 0 auto;
}

.header {
    display: flex;
    align-items: flex-start;
    justify-content: space-between;
    height: 64px;
}

.popup {
    display: none;
}
.popup::before {
    content: '';
    border: 10px solid transparent;
    border-bottom: 15px solid white;
    position: absolute;
    top: -24px;
    left: 515px;
}
.popup__item {
    width: 46%;
    height: 90px;
}
.popup__title_link {
    font-style: normal;
    font-weight: bold;
    font-size: 22px;
    line-height: 26px;
    color: $defaultText;
    text-decoration: none;
}
.popup__title {
    margin-bottom: 10px;
}
.popup__description_text {
    font-style: normal;
    font-weight: normal;
    font-size: 20px;
    line-height: 26px;
    color: $defaultText;
}

.header {
    display: flex;
    justify-content: space-between;
}
.container-header {
    padding: 0;
}

.header .btn {
    display: none;
    z-index: 1;
}
.header .logo__subtitle{
    display: none;
}
.header__logo {
    display: flex;
    align-items: center;
}
.logo__img {
    margin: 15px 18px 13px 15px;
    width: 48px;
    height: 48px;
}

.logo__title {
    font-size: 20px;
    line-height: 25px;
}
.burger-menu {
    display: block;
    border: none;
    height: 65px;
    width: 58px;
    border-left: 1px solid rgba(0, 0, 0, .15);
    border-bottom: 1px solid rgba(0,0,0,0.15);
    z-index: 3;
    background-repeat: no-repeat;
    background-position: 50% 50%;
    cursor: pointer;
    background-image: url("../img/burger2.svg");
    background-size: 40%;
    position: fixed;
    right: 0px;
    background-color: transparent;
}

.close-burger {
    width: max-content;
    position: fixed;
    right: 305px;
    z-index: 2;
    cursor: pointer;
    top: 16px;
    background: white;
    height: 24px;
}

.header__menu {
    display: none;
}

.header__menu_activate {
    display: block;
    position: fixed;
    right: 0;
    bottom: 0;
    z-index: 1;
    top: 0;
    background: rgba(0,0,0, .5);
    width: 100vw;
}

.header__menu-list {
    list-style: none;
    display: flex;
    flex-direction: column;
    overflow: auto;
    background-color: transparent;
}

.header__menu-list .header__menu_list-item {
    padding: 15px 0;
    width: 100%;
    text-align: center;
}
.header__menu-list .header__menu_link {
    text-decoration: none;
    font-size: 24px;
    display: block;
}


.header__menu .header__menu_submenu .menu-link__submenu::after{
    content: url("../img/Arrow.png");
    position: absolute;
    top: 125px;
    right: 94px;
}

.popup-mini-toggle {
    display: none;
}

.header__menu-list {
    position: fixed;
    width: 300px;
    background: $bgcBanner;
    top: 0;
    right: 0px;
    bottom: 0;
    animation: move .3s 1;
}

.burger-menu_activate {
    border-left: none;
}

.menu-link__submenu {
    cursor: pointer;
}

.popup-mini__list-item {
    padding: 8px 0;
}
.popup-mini__list-item-link {
    display: block;
    font-size: 19px;
    text-decoration: none;
}
/*-------*/

@include media($_768) {

    .popup {
        width: 612px;
        height: 280px;
        background: $white;
        box-shadow: 0px 11px 55px rgba(0, 0, 0, 0.07), 0px 29px 45px rgba(0, 0, 0, 0.08), 0px 13px 18px rgba(0, 0, 0, 0.15);
        border-radius: 8px;
        display: flex;
        align-items: flex-start;
        flex-wrap: wrap;
        justify-content: space-evenly;
        position: relative;
        align-content: space-around;
    }

    .header {
        height: auto;
    }
    .header__menu_list-item:hover {
        background-color: transparent;
    }
    .header__menu-list .header__menu_list-item {
        padding: 0;
        width: auto;
    }
    .header__menu-list {
        position: static;
        width: auto;
        overflow: visible;
    }

    .header__menu-list {
        display: none;
    }
    .close-burger {
        display: none;
    }
    .popup-mini {
        display: none;
    }
    .logo__img {
        margin: 0 18px 0 15px;
    }

    .header .header__logo .logo__text{
        font-size: 20px;
        line-height: 25px;
        height: 51px;
    }

    .header.logo__title {
        font-weight: bold;
    }

    .header.logo__subtitle {
        font-weight: normal;
    }

    .header__logo {
        display: flex;
        align-items: center;
        width: 340px;
    }

    .logo__img {
        margin-right: 15px;
    }

    .header__menu {
        display: block;
        margin-left: 20px;
        margin-top: 8px;
    }

    .header__menu-list {
        list-style: none;
        display: flex;
        flex-direction: row;
    }

    .header__menu-list .header__menu_link {
        text-decoration: none;
        line-height: 20px;
        font-size: 16px;
    }


    .header__menu-list li:first-child{
        font-weight: bold;
    }

    .header__menu-list li {
        display: inline-block;
        margin-right: 20px;
        padding: 0;
        width: auto;
        text-align: left;
    }


    .header__menu .header__menu_submenu::after{
        content: url("../img/Arrow.png");
        position: absolute;
        left: 66px;
        top: 0px;
    }

    .header__menu-list .header__menu_submenu {
        position: relative;
        padding-right: 20px;
    }

    .header__menu-list .header__menu_submenu .container-popup{
        opacity: 0;
        position: absolute;
        padding-top: 35px;
        left: -490px;
        z-index: -1;
        transition: .2s linear;
    }

    .header__menu-list .header__menu_submenu:hover .container-popup{
        z-index: 10;
        opacity: 1;
        transition: opacity .2s linear;
    }

    .popup__title_link:hover {
        color: $green;
    }

    .header .btn {
        display: block;
        margin-right: 15px;
    }
    .header .logo__subtitle{
        display: block;
    }
    .container-header {
        padding-top: 20px;
        max-width: 1100px;
    }
    .header button {
        margin-right: 35px;
    }
}

@include media($_992) {
    .header__menu-list .header__menu_link {
        line-height: 25px;
        font-size: 18px;
    }
    .header__menu {
        margin-left: 187px;
        margin-top: 8px;
    }

    .header__menu .header__menu_submenu::after{
        content: url("../img/Arrow.png");
        position: absolute;
        left: 74px;
        top: 3px;
    }
}

@include media($_1200) {
    .header button {
        margin-right: 0;
    }
    .header__menu {
        margin-left: 250px;
    }
    .container-header {
        padding-top: 39px;
    }
}
