.reviews {
  position: relative;
  padding-bottom: 66px;
  overflow-x: hidden;

  .reviews__bg {
    background-image: url("../img/slider-bg.png");
    background-repeat: no-repeat;
    width: 273px;
    height: 243px;
    margin: 40px auto 0;
  }
}

.reviews-slide {
  display: flex;
  flex-direction: column;
  align-items: center;
  outline: none;


  .reviews-title {
    color: $blueText;
    font-style: normal;
    font-weight: bold;
    font-size: 24px;
    line-height: 30px;
    padding-bottom: 28px;
    text-align: center;
  }

  .reviews-subtitle {
    font-weight: normal;
    font-size: 24px;
    line-height: 34px;
    color: $defaultText;
    text-align: center;
    padding-bottom: 34px;
    max-width: 750px;
    margin: 0 auto;
  }
  .reviews-author_name {
    font-weight: bold;
    font-size: 16px;
    line-height: 24px;
    text-align: center;
    color: $defaultText;
  }
  .reviews-author_work {
    font-style: normal;
    font-weight: normal;
    font-size: 16px;
    line-height: 24px;
    text-align: center;
    color: rgba(43, 41, 45, 0.6);
    padding-bottom: 30px;
  }
  .reviews__slider-dots {
    display: flex;
    width: 50px;
    height: 8px;
    justify-content: space-between;
    padding-bottom: 66px;

    .slider-dot {
      width: 8px;
      height: 8px;
      background: $defaultText;
      mix-blend-mode: normal;
      opacity: 0.2;
      border-radius: 50%;
    }

    .slider-dot-active {
      background: $blueText;
      opacity: 1;
    }
  }
}

@include media($_480) {
  .reviews {
    display: block;
    .reviews__bg {
      position: absolute;
      z-index: -1;
      top: 108px;
      right: 20px;
      margin-top: 0;
    }
  }
}

.slick-dots li button:before{
 background: $blueText;
  border-radius: 10px; 
  content: '';
  width: 10px;
  height: 10px;
}
.slick-dots li button {
  width: 10px;
  height: 10px;
}
.slick-dots li {
  width: 10px;
  height: 10px;
}

