.options {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-bottom: 29px;
  padding: 0 20px;
  background-color: $white;

  .options__title {
    font-style: normal;
    font-weight: bold;
    font-size: 40px;
    line-height: 40px;
    text-align: center;
    letter-spacing: -0.357143px;
    color: $defaultText;
    padding-bottom: 18px;
  }

  .options__subtitle {
    font-style: normal;
    font-weight: normal;
    font-size: 24px;
    line-height: 34px;
    text-align: center;
    color: $defaultText;
    padding-bottom: 28px;
    max-width: 555px;
  }

  .options-vacancies{
    margin-top: 39px;
    display: flex;
    flex-direction: column;
    align-items: center;
  }

  .options__vacancy {
    width: 290px;
    height: 140px;
    border: 1px solid rgba(43, 41, 45, 0.2);
    box-sizing: border-box;
    border-radius: 8px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 20px;
    cursor: pointer;
    padding: 0 25px;

    .vacancy-arrow {
      align-self: stretch;
      width: 50px;
      margin: 0 -25px;
      display: flex;
      align-items: center;
      background: #D5D4D5;
      justify-content: center;
      border-radius: 0px 8px 8px 0px;
    }

    .vacancy-name {
      width: 200px;
      font-style: normal;
      font-weight: bold;
      font-size: 22px;
      line-height: 28px;
      color: $defaultText;
    }
    .vacancy-description {
      width: 182px;
      font-style: normal;
      font-weight: normal;
      font-size: 18px;
      line-height: 24px;
      color: $defaultText;
      mix-blend-mode: normal;
      opacity: 0.6;
    }
  }

  .withoutContent {
    cursor: auto !important;
  }

  .options__vacancy:hover {
    background: $blue;
    box-shadow: 0px 11px 55px rgba(0, 0, 0, 0.07), 0px 13px 18px rgba(0, 0, 0, 0.15);
    border-radius: 8px;

    .vacancy-name {
      color: $white;
    }
    .vacancy-description {
      color: $white;
      opacity: 1;
    }
    .vacancy-arrow-svg {
      fill: $white;
      opacity: 1;
    }
  }

  .vacancyDesription {
    max-width: 700px;
    overflow: hidden;
    transition: all 1s;
    .job-title {
      padding-bottom: 10px;
      font-weight: bold;
      font-size: 18px;
      line-height: 18px;
      color: $defaultText;
    }

    p {
      padding-bottom: 12px;
    }

    ul {
      padding-left: 20px;
      padding-bottom: 16px;
    }

    .connect {
      display: inline-block;
      font-size: 18px;
      line-height: 26px;
      color: $blue;
      margin-bottom: 42px;
      font-weight: bold;
      cursor: pointer;
    }
  }

}

@include media($_480) {
  .options {
    margin-top: 10px;
    display: flex;
    flex-direction: column;
    align-items: center;
    margin-bottom: 89px;
    padding: 0 20px;

    .vacancy-wrap {
      width: 73vw;
      display: flex;
      justify-content: space-around;
      align-items: center;
    }

    .options__title {
      font-style: normal;
      font-weight: bold;
      font-size: 56px;
      line-height: 56px;
      text-align: center;
      letter-spacing: -0.5px;
      color: $defaultText;
      padding-bottom: 20px;
    }

    .options__subtitle {
      font-style: normal;
      font-weight: normal;
      font-size: 24px;
      line-height: 34px;
      text-align: center;
      color: $defaultText;
      padding-bottom: 28px;
      max-width: 555px;
    }

    .options-vacancies{
      margin-top: 70px;
    }

    .options__vacancy {
      min-width: 410px;
      height: 81px;
      border: 1px solid rgba(43, 41, 45, 0.2);
      box-sizing: border-box;
      border-radius: 8px;
      display: flex;
      justify-content: space-between;
      align-items: center;
      margin-bottom: 20px;
      cursor: pointer;
      padding: 0 25px;

      .vacancy-arrow {
        background: transparent;
      }
      .vacancy-name {
        width: 200px;
        font-style: normal;
        font-weight: bold;
        font-size: 22px;
        line-height: 28px;
        color: $defaultText;
      }
      .vacancy-description {
        width: 170px;
        font-style: normal;
        font-weight: normal;
        font-size: 18px;
        line-height: 24px;
        color: $defaultText;
        mix-blend-mode: normal;
        opacity: 0.6;
      }
    }

    .options__vacancy:hover {
      background: $blueText;
      box-shadow: 0px 11px 55px rgba(0, 0, 0, 0.07), 0px 13px 18px rgba(0, 0, 0, 0.15);
      border-radius: 8px;

      .vacancy-name {
        color: $white;
      }
      .vacancy-description {
        color: $white;
        opacity: 1;
      }
      .vacancy-arrow-svg {
        fill: $white;
        opacity: 1;
      }
    }
  }
}

@include media($_768) {
  .options {
    margin-top: 110px;

    .options-vacancies{
      margin-top: 110px;
    }
    .options__vacancy {
      width: 750px;
      .vacancy-name {
        width: 408px;
      }
      .vacancy-description {
        width: 232px;
      }

      .vacancy-arrow {
        margin: 0;
      }
    }
  }
}

