.vacancyDesription {
  max-width: 700px;
  animation-duration: .3s;
  animation-timing-function: linear;

  .btn {
    width: 130px;
    margin-bottom: 14px;
  }

  input {
    max-width: 360px;
    min-width: 200px;
    height: 48px;
    margin: 0 0 20px 0;
    box-sizing: border-box;
    border: 1px solid rgba(43, 41, 45, 0.2);
    border-radius: 2px;
  }

  input[placeholder] {
    padding-left: 14px;
    font-size: 18px;
    line-height: 13px;
    color: $defaultText;
  }

  form {
    display: flex;
    flex-direction: column;
  }

  .modal__img {
    padding-right: 15px;
    display: none;
  }
}

.options__modal {
  position: fixed;
  top: 0;
  right: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0,0,0, .3);
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 2;
}

.modal-close { 
  display: none;
}

.close {
  cursor: pointer;
  position: absolute;
  right: 15px;
  top: 15px;
}

.modal-window {
  background-color: $white;
  width: 90%;
  height: 292px;
  position: relative;
  box-shadow: 0px 11px 55px rgba(0, 0, 0, 0.07), 0px 13px 18px rgba(0, 0, 0, 0.15);
  border-radius: 8px;
  display: flex;
  justify-content: center;
  align-items: center;
}

.form__title {
  font-style: normal;
  font-weight: bold;
  font-size: 18px;
  line-height: 26px;
  color: $defaultText;
  margin-bottom: 10px;
}

/* ._hide {
} */
.animateIn {
  animation-name: fadeIn;
  animation-duration: .4s;
  animation-iteration-count: 1;
}
.animateOut {
  animation-name: fadeOut;
  animation-duration: .4s;
  animation-iteration-count: 1;
}

.vacancy-svg {
  transform: rotate(0deg);
  transition: transform 1s;
}
.rotateArrow {
  transform: rotate(90deg);
}

.animationShow {
  animation-name: animationShow;
  animation-duration: .4s;
}
.animationUnshow {
  animation-name: animationUnshow;
  animation-duration: .4s;
}

@include media($_480) {
  .vacancyDesription .modal__img {
    display: block;
  }
}

@include media($_768) {
  .vacancyDesription .modal__img {
    padding-right: 70px;
  }

  .modal-window {
    width: 700px;
  }
}