.preloader {
  width: 100vw;
  height: 100vh;
  z-index: 100;
  position: fixed;
  top: 0;
  left: 0;
  background: $defaultText;
  opacity: .8;
}

.loader {
  animation-duration: 1s;
  animation-name: rotate;
  animation-iteration-count: infinite;
  margin: 20% auto 0;
  width: 10%;
  height: 10%;
  animation-timing-function: linear;
  animation-play-state: running;
  display: block;
}

.preloaderTitle {
  margin: 20px 0;
  text-align: center;
  color: $white;
}

.loaded .preloader {
  display: none;
}

.loaded_hiding .preloader {
  transition: 0.3s opacity;
  opacity: 0;
}

.options-vacancies .loader {
  width: 100px;
  height: 100px;
}

.options-vacancies .preloaderTitle {
  color: $defaultText;
}