.logos {
  box-sizing: border-box;
  max-width: 321px;
  height: 189px;
  display: flex;
  flex-wrap: wrap;
  margin: 0 auto 76px;
  align-items: center;
  justify-content: center;

  .logos__logo {
    display: flex;
    align-content: center;
    justify-content: center;
    border: 1px solid #E3E3E3;
    border-top: none;
    width: 99px;
    height: 95px;
  }

  img {
    align-self: center;
    max-width: 78px;
    max-height: 55px;
  }
}

@include media($_480) {
  .logos {
    max-width: max-content;
    justify-content: center;
    .logos__logo {
      border: none;
      width: 30%;
    }

    img {
      width: max-content !important;
      height: max-content !important;
    }
  }
}

@include media($_768) {
  .logos {
    max-width: 1140px;
    height: 75px;
    display: flex;
    flex-wrap: nowrap;
    justify-content: space-around;
    align-items: center;
    margin-top: 43px;

    .logos__logo {
      width: auto;
      height: auto;
      padding: 0 12px;
    }

    img {
      max-width: 152px;
      max-height: 55px;
    }
  }
}
